import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { FotoTiradaPage } from '../pages/foto-tirada/foto-tirada';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
// Camera
import { Camera } from '@ionic-native/camera';
// Http
import { HttpModule } from '@angular/http';
//AngularFire2
import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule } from "angularfire2/auth";
import { firebaseConfig } from './firebase.config';
import { RecuperarSenhaPage } from '../pages/recuperar-senha/recuperar-senha';
import { VerificaEmailPage } from '../pages/verifica-email/verifica-email';
@NgModule({
  declarations: [
    MyApp,
    HomePage,
    FotoTiradaPage,
    LoginPage,
    RegisterPage,
    RecuperarSenhaPage,
    VerificaEmailPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/foto-tirada/foto-tirada.module#FotoTiradaPageModule', name: 'FotoTiradaPage', segment: 'foto-tirada', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/verifica-email/verifica-email.module#VerificaEmailPageModule', name: 'VerificaEmailPage', segment: 'verifica-email', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recuperar-senha/recuperar-senha.module#RecuperarSenhaPageModule', name: 'RecuperarSenhaPage', segment: 'recuperar-senha', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    FotoTiradaPage,
    LoginPage,
    RegisterPage,
    RecuperarSenhaPage,
    VerificaEmailPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Camera,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



